import React, { Component } from 'react';
export default class Courses extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
    <section id="courses">

      <section id="about">
         <div className="row">
                 <h2>Coding Profiles</h2>
                <div className="twelve columns collapsed">
                        <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                        {
                        resumeData.codingprofiles && resumeData.codingprofiles.map((item)=>{
                        return(
                                <div className="columns portfolio-item">
                                <div className="item-wrap">
                                <a href={item.link} target="_blank">
                                <img src={`${item.imgurl}`} className="item-img"/>
                                <div className="overlay">
                                        <div className="portfolio-item-meta">
                                        <a href={item.link} target="_blank"> <h5>{item.name}</h5></a>
                                        </div>
                                </div>
                                </a>
                                </div>
                                <a href={item.link} target="_blank"><h5>{item.name}</h5></a>
                                </div>
                        )
                        })
                        }
                        </div>
                </div>
        </div>

        
            


        <div className="row">
        
                <h2>Courses Attended</h2>


                <div className="nine columns main-col">

                                <div className="row">

                                        <div className="columns ">
                                                <p className="address">
                                                        <span>Operating Systems</span>
                                                <br></br>
                                                        <span>Theory of Computation</span>
                                                <br></br>
                                                        <span>Discrete Mathematics</span>
                                                <br></br>
                                                        <span>Databases and SQL</span>
                                                        </p>
                                        </div>

                                        <div className="columns " >
                                                <p className="address">
                                                        <span>Compiler Design</span>
                                                <br></br>
                                                        <span>Object Oriented Programming with C/C++</span>
                                                <br></br>
                                                        <span>Data Structures and Algorithms</span>
                                                <br></br>
                                                        <span>Computer Networks</span>
                                                        </p>
                                        </div>
                                
                                </div>

                                <div className="row">

                                        <h2>Certifications</h2>

                                        <p className="address">
                                                <ol>
                                                        <span>IBM-Basics of Machine Learning (Classification Algorithms) (May 2019) By Coursera</span>
                                                <br></br>
                                                        <span> Object Oriented Programming with C++ (June 2018) At Seed Infotech Pvt Ltd, Pune</span>
                                                </ol>
                                        </p>
                                </div>
                        

                </div>

         </div>
      </section>
   </section>
    );
  }
}